*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  color: var(--text-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a, p, h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  display: flex;
  flex-wrap: nowrap;
  height: 100vh;
}

main {
  height: 100%;
}

.site__content {
  padding: 0;
  margin: 0;
  width: calc(100vh - 60px);
  background-color: var(--white);
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0;
  // max-width: var(--container-width);
  width: 100%;
  height: fit-content;

  .block:last-child {
    margin-bottom: 20px;
  }
}

.section {
  height: 100%;
  padding: #{fluid(30px, 60px, 768px, 1440px)} #{fluid(30px, 60px, 768px, 1440px)};
  overflow-y: scroll;
  transition: var(--transition);
}

.block {
  margin-bottom: 80px;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

// 

.z10 {
  z-index: 10;
}

.noTranslate {
  transform: translateX(0) !important;
}

.noScaleRotate {
  transform: scale(-1, 1);
}

.manrope {
  font-family: "Manrope", sans-serif;
}

.op0 {
  opacity: 0;
}

.closedPage {
  opacity: 0;
  transform: translateY(-2%);
}

.dnone {
  display: none;
}

.green {
  color: var(--green);
}

.btn {

}

.box-shadow1 {
  -webkit-box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);

    &:hover {
        -webkit-box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
        -moz-box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
        box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
    }
}

.box-shadow--green {
  -webkit-box-shadow: 0px 1px 10px 2px rgba(69, 215, 61, 0.2);
  -moz-box-shadow: 0px 1px 10px 2px rgba(69, 215, 61, 0.2);
  box-shadow: 0px 1px 10px 2px rgba(69, 215, 61, 0.2);
}

h2 {
  font-size: #{fluid(30px, 48px, 768px, 1440px)};
  margin-bottom: 24px;
}

.HTML-tag {
  background-color: rgb(236, 104, 50, 0.3);
  color: rgb(236, 104, 50);
}
.CSS-tag {
  background-color: rgb(59, 115, 189, 0.3);
  color: rgb(59, 115, 189);
}
.JavaScript-tag {
  background-color: rgb(220, 171, 58, 0.3);
  color: rgb(220, 171, 58);
}
.GULP-tag {
  background-color: rgb(231, 84, 91, 0.3);
  color: rgb(231, 84, 91);
}
.SCSS-tag {
  background-color: rgb(216, 133, 174, 0.3);
  color: rgb(216, 133, 174);
}
.MaxGraph-tag {
  background-color: rgb(0, 0, 0, 0.3);
  color: rgb(255, 255, 255);
}
.github-tag {
  background-color: rgb(63, 63, 63, 0.3);
  color: rgb(63, 63, 63);
}
.Figma-tag {
  background-color: rgb(43, 211, 138, 0.3);
  color: rgb(43, 211, 138);
}
.Photoshop-tag {
  background-color: rgb(90, 202, 252, 0.3);
  color: rgb(90, 202, 252);
}
.adaptive-tag {
  background-color: rgb(180, 154, 103, 0.3);
  color: rgb(180, 154, 103);
}
.crossbrowser-tag {
  background-color: rgb(78, 135, 140, 0.3);
  color: rgb(78, 135, 140);
}
.valid-tag {
  background-color: rgb(85, 91, 110, 0.3);
  color: rgb(85, 91, 110);
}

@media (hover: hover) {
  .box-shadow1 {
      &:hover {
          -webkit-box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
          -moz-box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
          box-shadow: 0px 4px 10px 2px rgba(17, 17, 17, 0.2);
      }
  }
}

@include mobile {
  .site__content {
    width: 100%;
    height: calc(100% - 60px);
  }

  .section {
    padding: 15px 15px;
  }
}