.top-block {

}

.top-block__container {
    height: 100%;
}

.top-block__content {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: center;
    gap: #{fluid(20px, 40px, 768px, 1440px)};
}

h1 {
    display: flex;
    flex-direction: column;
    gap: 8px;
    min-width: fit-content;
    
    span {
        &:nth-child(1) {
            font-size: #{fluid(18px, 36px, 768px, 1440px)};
            font-family: var(--inter);
            margin-left: 10%;
            font-weight: 400;
        }

        &:nth-child(2) {
            font-size: #{fluid(45px, 90px, 768px, 1440px)};
            color: var(--green);
        }
        
        &:nth-child(3) {
            text-align: center;
            font-size: #{fluid(18px, 36px, 768px, 1440px)};
            font-family: var(--inter);
            margin-left: 50%;
            font-weight: 400;
        }
        
        &:nth-child(4) {
            text-align: right;
            font-size: #{fluid(24px, 48px, 768px, 1440px)};
            margin-right: 10%;
            font-weight: 400;
        }
    }
}

.top-block__my-images {
    width: #{fluid(325px, 650px, 768px, 1440px)};
    height: #{fluid(325px, 650px, 768px, 1440px)};
    border-radius: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
    cursor: pointer;

    &::before {
        content: "";
        width: #{fluid(250px, 500px, 768px, 1440px)};
        height: #{fluid(250px, 500px, 768px, 1440px)};
        border-radius: 45%;
        background-color: white;
        z-index: -1;
        position: absolute;
        bottom: 0;
        left: calc(50% - #{fluid(125px, 250px, 768px, 1440px)});
        transition: var(--transition);
    }

    &:hover {
        .top-block__my-image {
            &:first-child {
                opacity: 0;
            }

            &:last-child {
                opacity: 1;
            }
        }
        
    }
}

.top-block__my-image {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    transition: 1s;

    &:first-child {
        opacity: 1;
    }

    &:last-child {
        opacity: 0;
    }
}

@media (hover: hover) {
    .top-block__my-images {    
        &:hover {
            .top-block__my-image {
                &:first-child {
                    opacity: 0;
                }
    
                &:last-child {
                    opacity: 1;
                }
            }
        }
    }
}

@include small-tablet {
    .top-block__content {
        flex-direction: column-reverse;
        justify-content: space-evenly;
    }
}