.facts {
    &__content {
        display: flex;
        gap: 32px;
    }

    &__main {
        background-color: white;
        border-radius: 16px;
        padding: 18px 36px;
        max-width: 60%;
        width: 100%;
        display: flex;
        flex-direction: column;
        width: fill-available;
        gap: 16px;

        -webkit-box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);
        -moz-box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);
        box-shadow: 0px 2px 8px 0px rgba(34, 60, 80, 0.2);
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        height: fit-content;
        gap: 20px;
    }

    &__stack {
        margin-top: auto;
        display: flex;
        flex-wrap: wrap;
        gap: 5px;
    
        span {
            font-size: 18px;
            padding: 3px 6px;
            border-radius: 3px;
        }
    }
}

.my-description {
    font-size: #{fluid(16px, 24px, 1024px, 1440px)};
}

.facts-card {
    border-radius: 16px;
    background-color: white;
    padding: 18px 36px;
    width: fit-content;
    transition: var(--transition);
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    justify-content: center;
    min-width: 175px;
    flex: 1 1 calc(50% - 10px);
    aspect-ratio: 6/5;
    position: relative;

    &__svg {
        fill: var(--green-white);
        position: absolute;
        top: -10px;
        left: -10px;
        background-color: var(--white);
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        border: 3px solid white;
    }

    &__param {
        font-size: #{fluid(16px, 24px, 1024px, 1440px)};
    }

    &__num {
        font-size: #{fluid(60px, 96px, 1024px, 1440px)};
    }
}

//! swiper gallery

.about__swipers {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.gallery-swiper {
    border-radius: 16px;
}

.gallery {
        &__slide {
        overflow: hidden;
        aspect-ratio: 1/1;
        position: relative;
        border-radius: 16px;

        img {
            object-fit: cover;
        }
    }

    &__prev, &__next {
        width: 40px;
        height: 40px;
        background-color: white;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: var(--transition);
        
        svg {
            fill: var(--green);
            transition: var(--transition);
        }
    }
}

.gallery-swiper__btns {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    gap: 8px;
    z-index: 1;
}

.gallery-pagination__container {
    display: flex;
    justify-content: flex-end;
    margin-top: 24px;
    width: fill-available;
}

.gallery__pagination {
    max-width: 300px;
    display: flex;
    flex-wrap: nowrap;
    gap: 4px;
    justify-content: space-between;
    position: initial !important;

    .swiper-pagination-bullet {
        padding: 0;
        margin: 0;
        width: 100%;
        height: 6px;
        border-radius: 3px;
        background-color: var(--green);
    }
}

//! reviews

.reviews__container {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.review {
    background-color: white;
    padding: 18px 24px;
    border-radius: 16px;
    min-width: 300px;
    flex: 1 1 calc(50% - 10px);
    display: flex;
    flex-direction: column;
    gap: 16px;
    transition: var(--transition);
    cursor: pointer;

    &__author-info {
        display: flex;
        gap: 10px;
    }

    &__avatar {
        width: 40px;
        height: 40px;
        border-radius: 100%;
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid var(--green);

        svg {
            fill: white;
            stroke: black;
        }
    }

    &__author {
        p {
            &:last-child {
                font-family: var(--inter);
            }
        }
    }

    &__text {
        font-family: var(--inter);
    }
}

// ! socials

.socials {
    &__container {
        display: flex;
        gap: 16px;
    }

    &__link {
        aspect-ratio: 1/1;
        width: 60px;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: var(--transition);
        
        svg {
            fill: white;
            transition: ease-in-out var(--transition);
        }
    }

    &__tg {
        background-color: var(--telegram);
    }

    &__wa {
        background-color: var(--whatsapp);
    }

    &__vk {
        background-color: var(--vkontakte);
    }

    &__mail {
        background-color: var(--mail);
    }

    &__gh {
        background-color: var(--github);
    }
}

@media (hover: hover) {
    .gallery {
        &__prev, &__next {            
            &:hover {
                background-color: var(--green);

                svg {
                    fill: white;
                }
            }
        }
    }

    .socials {
        &__link {
            &:hover {
                -webkit-box-shadow: 0px 1px 8px 0px rgba(17, 17, 17, 0.2);
                -moz-box-shadow: 0px 1px 8px 0px rgba(17, 17, 17, 0.2);
                box-shadow: 0px 1px 8px 0px rgba(17, 17, 17, 0.2);
    
                svg {
                    rotate: 360deg;
                }
            }
        }
    
        &__tg {    
            &:hover {
                background-color: white;
    
                svg {
                    fill: var(--telegram);
                }
            }
        }
    
        &__wa {    
            &:hover {
                background-color: white;
    
                svg {
                    fill: var(--whatsapp);
                }
            }
        }
    
        &__vk {   
            &:hover {
                background-color: white;
    
                svg {
                    fill: var(--vkontakte);
                }
            }
        }
    
        &__mail {   
            &:hover {
                background-color: white;
    
                svg {
                    fill: var(--mail);
                }
            }
        }
    
        &__gh {  
            &:hover {
                background-color: white;
    
                svg {
                    fill: var(--github);
                }
            }
        }
    }
}

@include tablet {
    .facts {
        &__content {
            flex-direction: column;
        }

        &__main {
            max-width: none;
            min-height: 300px;
        }

        &__card {
            flex-basis: calc(25% - 60px/4);
            aspect-ratio: unset;
        }

        &__stack {
            span {
                font-size: 14px;
            }
        }
    }
}
