.header {
    width: 60px;
    background-color: var(--dark);
    height: 100vh;
    padding: 20px 0;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 10;

    -webkit-box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
    -moz-box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
    box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
}

.header__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    z-index: 10;
}

.header__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--dark);
    z-index: 10;
}

.header__link {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    padding: 10px;
    transition: var(--transition);

    svg {
        fill: var(--white);
        transition: var(--transition);
        cursor: pointer;
    }

    &--active {

        position: relative;

        &::before {
            content: "";
            height: 40px;
            width: 10px;
            border-radius: 10px;
            background-color: var(--green);
            position: absolute;
            top: 10px;
            left: -5px;
        }

        svg {
            fill: var(--green);
            margin-left: 6px;
        }
    }
}

.header__expand {
    margin-top: auto;
    height: fit-content;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: unset;
    border: none;
    scale: (-1 -1);
    cursor: pointer;
    z-index: 10;
    background-color: var(--dark);

    svg {
        fill: white;
        transition: var(--transition);
    }
}

.header__texts {
    position: absolute;
    top: 0;
    left: 100%;
    background-color: var(--dark);
    padding: 20px 0;
    height: 100%;
    transform: translateX(-200%);
    transition: var(--transition);

    -webkit-box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
    -moz-box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
    box-shadow: 0px 2px 7px 5px rgba(17, 17, 17, 0.2);
}

.header__text {
    height: 60px;
    display: flex;
    align-items: center;
    padding: 5px 60px 0 20px;
    color: var(--white);
    transition: var(--transition);
    position: relative;


    &--active {
        color: var(--green);
    }

    button {
        background-color: unset;
        border: none;
        position: absolute;
        top: 0;
        left: -60px;
        height: 100%;
        width: calc(100% + 60px);
        cursor: pointer;

    }
}

@media (hover: hover) {
    .header__text {
        &:hover {
            color: var(--green-white);  
            
            .header__link svg {
                fill: var(--green-white);
            }
        }
    }
    .header__expand {
        &:hover {
            svg {
                fill: var(--green-white);
            }
        }
    }
    .header__link {
        svg {  
            &:hover {
                fill: var(--green-white);
            }
        }
    }
}

@include mobile {
    .header {
        position: fixed;
        bottom: 0;
        top: unset;
        left: 0;
        height: 60px;
        width: 100vw;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .header__nav {
        flex-direction: row;
        justify-content: center;
    }

    .header__link {
        &--active {    
            &::before {
                content: "";
                height: 10px;
                width: 40px;
                border-radius: 10px;
                background-color: var(--green);
                position: absolute;
                left: 10px;
                bottom: -5px;
                top: unset;
            }

            svg {
                margin-left: 0;
                margin-bottom: 6px;
            }
        }
    }

    .header__expand {
        display: none;
    }

    .header__texts {
        display: none;
    }
}