.portfolio {
    &__container {

    }

    &__content {
        display: flex;
        gap: 20px;
        flex-direction: column;
    }

    &__project {
        display: flex;
        gap: #{fluid(40px, 80px, 1024px, 1440px)};
        width: 100%;
        flex-wrap: wrap;
        align-items: flex-start;
    }

    &__info {
        min-width: 300px;
        flex-basis: calc(40% - #{fluid(20px, 40px, 1024px, 1440px)});
        width: fill-available;
        position: relative;
        height: fit-content;

        &::before {
            content: "";
            width: 4px;
            height: 100%;
            border-radius: 4px;
            background-color: var(--green);
            position: absolute;
            top: 0;
            left: -30px;
            opacity: 1 !important;
        }
    }

    &__descriptions {
        display: flex;
        flex-direction: column;
        gap: 16px;
        transition: var(--transition);
    }

    &__swiper {
        aspect-ratio: 16/9;
        flex-basis: calc(60% - #{fluid(20px, 40px, 1024px, 1440px)});
        width: 100%;
        border-radius: 4px;
    }

    &__wrapper {
    }

    &__slide {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__swiper-btns {
        z-index: 1;
        top: 20px;
        right: 20px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 8px;
        width: 100%;
    }

    &__prev, &__next {
        width: 40px;
        height: 40px;
        background-color: var(--green);
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: var(--transition);
        cursor: pointer;
        
        svg {
            transition: var(--transition);
            fill: white;
        }
    }

    &__next {
    }
}

.project {
    &__info {
        display: flex;
        flex-direction: column;
        gap: 8px;

        p, a {
            font-family: var(--inter);
        }
    }

    &__name {
    }

    &__age {
    }

    &__technologies {
        display: flex;
        gap: 4px;
        flex-wrap: wrap;
    }

    &__description {
    }

    &__link {   
        a {
            color: var(--green);
            transition: var(--transition);
        }
    }
}

.technologies__list {
    height: fit-content;
    display: flex;
    gap: 4px;
    flex-wrap: wrap;

    p {
        border-radius: 3px;
        padding: 3px 6px;
        width: fit-content;
        font-family: var(--font-family);
    }
}

@media (hover: hover) {
    .project {    
        &__link {   
            a {                
                &:hover {
                    color: var(--green-white);
                }
            }
        }
    }
    .portfolio {    
        &__prev, &__next {            
            &:hover {
                background-color: white;
                
                svg {
                    fill: var(--green);
                }
            }
        }
    }
}

@include tablet {
    .portfolio__project {
        flex-direction: column-reverse;
    }

    .portfolio {
        &__info {
            &::before {
                left: -20px;
            }
        }
    }
}

@include mobile {
    .portfolio {
        &__info {
            &::before {
                display: none;
            }
        }
    }
}