// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Activist", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --green: #45D73D;
  --dark: #111111;
  --white: #F5F3F5;
  --text-color: rgb(0, 0, 0, 0.9);
  --green-white: color-mix(in srgb, var(--white) 50%, var(--green));
  --telegram: #0088CC;
  --whatsapp: #4FCE5D;
  --vkontakte: #4376A6;
  --mail: #E54A3F;
  --github: #3f3f3f;

  // fonts 
  --arial: "arial", sans-serif;
  --inter: "Inter", sans-serif;

  // transition
  --transition: 0.3s;
}
